<template>
    <div class="contract">
        <Header tit="Hp ng vay" color="1"></Header>
        <div class="content" v-html="data.content"></div>
        <div class="content2" v-show="data.content">

            <p>Thi gian k{{data.sign_date && new Date(Date.parse(data.sign_date)).toLocaleString('vi-VN')}}</p>
            <p v-if="data.money">S tin vay (Bng s/VND){{data.money}}</p>
            <p v-if="data.rate">Li sut hng thng (%){{data.rate}}</p>
            <p v-if="data.month">K hn vay (thng){{data.month}}</p>

            <p>S in thoi{{data.mobile}}</p>
            <p>S nhn din{{data.id_card_num}}</p>


            <p>K ca bn A</p>
            <img :src="$ht_ip + data.sign_img_url" alt="">
            
            <p>Ch k cng ty cho vay</p>

              <div class="seal">
                <img :src="data.seal" >
            </div>

        </div>
    </div>
</template>
<script>
import Header from "../public/header";
export default {
    components: { Header },
    data(){
        return{
            data:{}
        }
    },
    mounted(){
        this.get_data();
    },
    methods:{
        get_data(){
            this.$get(this.$path.contact).then(res => {
                if (res.success) {
                    this.data = res.data;
                }
            });
        }
    }
}
</script>
<style lang="scss" scoped>
.contract{
    .content2{
        margin-top:1rem;
        p{
            color: red;
            margin-bottom: .1rem;
        }
    }
    .seal{
        position: relative;
        img{
            position:absolute;
            right:0;
            height: 6.6rem;
        }
    }
}
</style>



