<template>
    <div class="login">
        <Header tit="ng k" color="1"></Header>
        <div class="tit_e">ng k</div>

        <!-- <form> -->
        <div class="register_box box_shadow">
            <div class="input_bar">
                <div class="item">
                    <div class="tit">S in thoi</div>
                    <input type="text" v-model="phone"   maxlength="10" placeholder="Hy nhp s in thoi di ng" />
                </div>
                <div class="item get_code">
                    <div class="tit">m xc nhn Captcha</div>
                    <input type="text" v-model="img_code" placeholder="Hy nhp m s kim tra  ha." />
                    <img :src="$path.captcha" alt />
                </div>
                <!-- <div class="item get_code">
                    <div class="label"></div>
                    <input type="text" v-model="sms_code" placeholder="" />
                    <button
                        @click="sendRegSms"
                        :disabled="code_count_down != 0"
                        class="syellow_color"
                    >{{code_count_down > 0 ? code_count_down+ 's' : ''}}</button>
                </div> -->
                <div class="item">
                    <div class="tit">Nhp mt khu</div>
                    <input type="password" v-model="password" placeholder="Xin hy t mt khu s 6-16" />
                </div>
                <div class="item">
                    <div class="tit">M mi</div>
                    <input type="text" v-model="invite_code" placeholder="M mi" />
                </div>
            </div>
        </div>
        <BigBtn @click="click_BigBtn" class="btn" label="ng k"></BigBtn>
        <div class="go_login" @click="go_login">K v.</div>
        <!-- </form> -->
    </div>
</template>

<script>
import Header from "../public/header";
import BigBtn from "../public/button";
export default {
    components: { Header, BigBtn },
    data() {
        return {
            phone: "", //String    
            password: "",
            sms_code: "888888", //String    
            img_code: "", //String    
            invite_code:"",
            code_count_down: 0, //
            play: null //
        };
    },
    methods: {
        forget() {
            this.$router.push({ name: "forget" });
        },
        //
        sendRegSms() {
            if (!this.img_code) {
                return this.$alert("Hy nhp m s kim tra  ha");
            }
            this.$post(this.$path.mobilecode, {
                code: this.img_code
            }).then(res => {
                if (res.success) {
                    this.$alert("Gi thnh cng ");
                    this.code_count_down = 60;
                    this.count_down();
                }
            });
        },
        count_down() {
            this.play = setInterval(() => {
                if (this.code_count_down > 0) {
                    this.code_count_down--;
                } else {
                    clearInterval(this.play);
                }
            }, 1000);
        },
        go_login() {
            this.$router.go(-1);
        },
        //
        click_BigBtn() {
            if (this.password1 !== this.password2) {
                return this.$alert("Hai mt khu khng khp vi nhau. ");
            }
            
            if (!this.phone || this.phone.length<9 || this.phone.length>10) {
               // return this.$alert("Hy nhp s in thoi di ng ");
                return this.$alert("S in thoi di ng c ti thiu 9-10 ch s ");
            }
            if (!this.password || this.password.length<6) {
                return this.$alert("Nhp mt khu i. ");
            }
            if (!this.sms_code) {
                return this.$alert("Hy nhp m kim tra SMS");
            }
            if (!this.img_code) {
                return this.$alert("Hy nhp m  kim tra nh.");
            }
            
            if (!this.invite_code) {
                return this.$alert("Hy nhp M mi.");
            }
            this.$post(this.$path.register, {
                mobile: this.phone,
                pass: this.password,
                mobile_code: this.sms_code,
                code: this.img_code,
                invite_code:this.invite_code
            }).then(res => {
                debugger
                if (res.success) {
                    this.$alert(" ng nhp thnh cng ");
                    setTimeout(() => {
                        //this.$router.push({ name: "my" });
                        this.$router.push({ path: "/my" });
                    }, 2000);
                }
            });
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "./login.scss";
.login {
    .get_code {
        border: none;
    }
    .register_box {
        width: 100%;
        background: #ffffff;
        border-radius: 0.2rem;
        .input_bar {
            padding-left: 0.3rem;
            background: #ffffff;
            border-radius: 0.16rem;
            margin-top: 0.3rem;
            .item {
                width: 100%;
                height: 1.16rem;
                border-bottom: 1px #e5e5e5 solid;
                .label {
                    width: 1.8rem;
                    height: 100%;
                    line-height: 1.1rem;
                    font-size: 0.32rem;
                    float: left;
                }
                input {
                    float: left;
                    margin-left: 0.2rem;
                    width: 4rem;
                    height: 100%;
                    margin-top: 0;
                }
            }
            .get_code {
                display: flex;
                flex-direction: row;
                input {
                    width: 2.75rem;
                }
                button {
                    width: 1.6rem;
                    height: 0.64rem;
                    border: 1px #bc7c1c solid;
                    background: none;
                    border-radius: 0.4rem;
                    margin-top: 0.26rem;
                    margin-right: 0.2rem;
                    font-size: 0.2rem;
                }
                img{
                    width: 1.6rem;
                    height: .6rem;
                    margin-top: .28rem;
                }
            }
            .no_border {
                border: none;
            }
        }
    }
    .go_login {
        color: #666666;
        margin-top: -0.2rem;
    }
}
</style>



