<template>
    <div class="login">
        <Header tit="ng nhp" color="1" back="false"></Header>
        <div class="tit_e">ng nhp</div>

        <div class="item_tit margin">S ti khon</div>
        <div class="item">
            <input type="text" v-model="phone" maxlength="11" placeholder="Th nhn vin hay s di ng " />
        </div>
        <div class="item_tit">mt khu </div>
        <div class="item">
            <input type="password" v-model="pass"  placeholder="Hy nhp mt khu." />
        </div>
        <BigBtn label="ng nhp" class="btn"></BigBtn>
        <div class="op">
            <div class="register" @click="register">ng k ti khon</div>
            <!-- <div class="forget" @click="forget"></div> -->
        </div>
    </div>
</template>

<script>
import Header from "./public/header";
import BigBtn from "./public/button";
export default {
    components: { Header, BigBtn },
    data() {
        return {
            phone: "", //String    
            pass: "" //String    
        };
    },
    methods: {
        forget() {
            this.$router.push({ name: "password",query:{
                forget:1
            } });
        },
        register() {
            this.$router.push({ name: "register" });
        },
        click_BigBtn() {
            if (!this.phone) {
                return this.$alert("Hy nhp s in thoi di ng ");
			}
			if (!this.pass) {
                return this.$alert("Nhp mt khu i. ");
            }
            this.$post(this.$path.login, {
                mobile: this.phone,
                pass: this.pass
            }).then(res => {
                if (res.success) {
                    this.$router.push({ name: "home" });
                }
            });
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "./login/login.scss";
</style>



