<template>
    <div class="login">
        <Header tit="Qun mt khu " color="1"></Header>
        <div class="item_tit">S di ng</div>
        <div class="item get_code">d
            <input type="text" v-model="phone" placeholder="Hy nhp s in thoi di ng" />
            <button
                @click="sendPswSms"
                :disabled="code_count_down != 0"
                class="syellow_color"
            >{{code_count_down > 0 ? code_count_down+ 'sC th gi sau' : 'Ly mt khu '}}</button>
        </div>
        <div class="item_tit">Mt khu</div>
        <div class="item">
            <input type="text" v-model="code" placeholder="Hy nhp mt m kim tra." />
        </div>
        <BigBtn label="bc k tip " class="btn" :disabled="!code"></BigBtn>
    </div>
</template>

<script>
import Header from "../public/header";
import BigBtn from "../public/button";
export default {
    components: { Header, BigBtn },
    data() {
        return {
            phone: "", //
            code: "", //
            code_count_down: 0, //
            play: null //
        };
    },
    beforeDestroy() {
        clearInterval(this.play);
    },
    methods: {
        click_BigBtn() {
            this.$router.push({
                name: "forgetpassword",
                query: { code: this.code, phone: this.phone }
            });
        },
        sendPswSms() {
            if (!this.phone) {
                return this.$alert("Hy nhp s in thoi di ng ");
            }
            this.$post(this.$path.sendPswSms, {
                phone: this.phone
            }).then(res => {
                if (res.success) {
                    this.$alert("Gi thnh cng ");
                    this.code_count_down = 60;
                    this.count_down();
                }
            });
        },
        count_down() {
            this.play = setInterval(() => {
                if (this.code_count_down > 0) {
                    this.code_count_down--;
                } else {
                    clearInterval(this.play);
                }
            }, 1000);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "./login.scss";
.get_code {
    display: flex;
    justify-content: space-between;
    input {
        width: 2.75rem;
    }
    button {
        width: 1.6rem;
        height: 0.64rem;
        border: 1px #bc7c1c solid;
        background: none;
        border-radius: 0.4rem;
        margin-top: 0.26rem;
        margin-right: 0.2rem;
        font-size: 0.2rem;
    }
    img {
        width: 1.6rem;
        height: 0.6rem;
        margin-top: 0.28rem;
    }
}
</style>



